body {
  user-select: none;
  background: #000;/*rgb(31, 31, 31);*/
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.nocursor p {
  font-size: 1.5em !important;
}
.nocursor input {
  color: transparent;
  text-shadow: 0 0 0 #fff;
  font-family: monospace;
  font-size: 1.7em !important;
  cursor: default;
}
.nocursor input:focus {
  outline: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: 'Stellar Regular';
  font-style: normal;
  font-weight: normal;
  src: url('/about/res/font/Stellar-Regular.otf') format('opentype'); /* IE9 Compat Modes */
}

@font-face {
  font-family: 'Stellar Light';
  font-style: normal;
  font-weight: 100;
  src: url('/about/res/font/Stellar-light.otf') format('opentype'); /* IE9 Compat Modes */
}